<template>
  <div>
    <Fieldset
      v-for="(item, itemIndex) of items"
      :key="item.id"
      class="my-4"
      style="min-width: 0"
    >
      <template #label>{{ item.name }}</template>
      <template #default>
        <BulkQuoteItemTable
          :items="item.items"
          @update:items="handleUpdateItem(itemIndex, $event)"
          @delete-item="$emit('deleteItem', $event)"
          @edit-item="$emit('editItem', $event)"
          @reconcile-item="$emit('reconcileItem', $event)"
          :percentage="percentage"
          :disabled="disabled"
          :disabled-status="disabledStatus"
          :reconcile="reconcile"
        />
      </template>
    </Fieldset>
  </div>
</template>
<script>
import { computed, defineComponent, reactive } from 'vue'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'

import BSlider from 'components/BSlider/BSlider.vue'
import BTable from 'components/BTable/BTable.vue'
import BulkQuoteItemTable from 'components/BulkQuote/Item/BulkQuoteItemTable.vue'

export default defineComponent({
  name: 'BulkQuoteItem',
  components: {
    Fieldset,
    BSlider,
    BTable,
    BulkQuoteItemTable
  },
  props: {
    items: {
      type: Array
    },
    percentage: {
      type: Number
    },
    disabled: {
      type: Boolean,
      default: false
    },
    disabledStatus: {
      type: Boolean,
      default: false
    },
    reconcile: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:percentage',
    'update:items',
    'deleteItem',
    'editItem',
    'reconcileItem'
  ],
  setup(props, { emit }) {
    const data = reactive({
      items: []
    })
    const items = computed({
      get: () => (props.items && props.items) || data.items,
      set: (newItems = []) => {
        const newFilteredItems = newItems.filter(
          (item) => item.items.length > 0
        )
        emit('update:items', newFilteredItems)
        data.items = newFilteredItems
      }
    })
    const hasOneOrMoreItems = computed(() => items.value.length > 0)

    const handleUpdateItem = (itemIndex, updatedItems) => {
      const currentItems = [...items.value]
      currentItems[itemIndex].items = updatedItems
      items.value = currentItems
    }

    return {
      data,
      items,
      hasOneOrMoreItems,
      handleUpdateItem
    }
  }
})
</script>
<style scoped lang="scss"></style>
