<template>
  <BTable
    :headers="tableHeaders"
    :items="tableData"
    min-width="auto"
    :class="[preview && 'table-preview']"
    :time-stamp="false"
  >
    <template #header="{ header }">
      <template v-if="header.key !== 'product_name'">
        <th
          :key="header.key"
          :class="['number-column', preview && 'column-normal']"
          :style="{
            minWidth: header.key === 'qty' && '100px'
          }"
        >
          <template v-if="header.key.startsWith('condition_') && !preview">
            <span class="flex items-center justify-center gap-2">
              {{ header.column_name }}
              <Tooltip placement="top" :disabled="!header?.meta?.rules?.length">
                <i
                  :class="[
                    'material-icons cursor-pointer',
                    !header?.meta?.rules?.length && 'opacity-50'
                  ]">
                  info
                </i>
                <template #content>
                  <div class="max-w-md">
                    <ul class="list">
                      <li
                        v-for="(term, termIndex) of header?.meta?.rules || []"
                        :key="termIndex"
                        class="text-white"
                      >
                        <span v-html="term"></span>
                      </li>
                    </ul>
                  </div>
                </template>
              </Tooltip>
            </span>
          </template>
          <template v-else>
            {{ header.column_name }}
          </template>
        </th>
      </template>
      <template v-else>
        <th :key="header.key" :class="[preview && 'column-normal']">
          {{ header.column_name }}
        </th>
      </template>
    </template>
    <template #column="{ key, column, row, rowIndex }">
      <template v-if="preview">
        <td v-if="key.startsWith('condition_')" class="number-column">
          <Currency :value="column?.price"></Currency>
        </td>
        <td
          v-else-if="key === 'qty'"
          class="number-column"
          :style="{ minWidth: '70px' }"
        >
          {{ column }}
        </td>
        <td
          v-else
          class="font-semibold"
          :style="{ minWidth: '300px', maxWidth: '300px' }"
        >
          {{ column }}
        </td>
      </template>
      <template v-else>
        <td v-if="key === 'product_name'" :style="{ minWidth: '300px' }">
          <template v-if="row?.is_custom === 1">
            <Input
              :key="`input-name-${rowIndex}`"
              :model-value="column"
              @update:model-value="updateName(rowIndex, $event)"
              block
              :disabled="disabled"
              class="mb-6"
            />
          </template>
          <template v-else>
            {{ column }}
          </template>
        </td>
        <td v-else-if="key === 'qty'" :style="{ minWidth: '70px' }">
          <Input
            :key="`input-qty-${rowIndex}`"
            :model-value="column"
            @update:model-value="updateQty(rowIndex, $event)"
            type="number"
            right
            block
            :disabled="disabled || disabledStatus"
            class="mb-6"
          />
        </td>
        <td
          v-else-if="key.startsWith('condition_')"
          :style="{ minWidth: '150px' }"
        >
          <Input
            :key="`input-price-${rowIndex}`"
            :model-value="column?.price"
            @update:model-value="
              updateConditionPrice(rowIndex, column, $event, {
                isManuallyPriced: true
              })
            "
            type="number"
            :currency="true"
            block
            :disabled="disabled || disabledStatus"
          />
          <small
            :style="{ opacity: column.is_manually_priced ? '1' : '0' }"
            class="text-grey-03 text-right block mt-2"
            >Manually priced</small
          >
        </td>
        <td v-else>
          {{ column }}
        </td>
      </template>
    </template>
    <template #footerRows>
      <!-- Total Row -->
      <tr class="b-table__row">
        <td
          v-for="(column, columnIndex) of tableFooter"
          :key="`total_${columnIndex}`"
          :class="[columnIndex > 0 && 'number-column']"
        >
          <template
            v-if="columnIndex > 1 && columnIndex < tableFooter.length - 1"
          >
            <Currency class="font-medium" :value="column"></Currency>
          </template>
          <template v-else>
            <span class="font-medium">{{ column }}</span>
          </template>
        </td>
      </tr>
      <!-- Commission Row -->
      <tr class="b-table__row">
        <td
          v-for="(column, columnIndex) of commissionFooter"
          :key="`commission_${columnIndex}`"
          :class="[columnIndex > 0 && 'number-column']"
        >
          <template
            v-if="columnIndex > 1 && columnIndex < commissionFooter.length - 1"
          >
            <Currency class="font-medium text-blue-600" :value="column"></Currency>
          </template>
          <template v-else>
            <div class="flex gap-2 items-center">
              <span class="font-medium">{{ column }}</span>
              <Tooltip v-if="columnIndex === 0" placement="top">
                <template #content>
                  <div :style="{ maxWidth: '300px' }">
                    <p>This commission value is subject to change at any time.</p>
                  </div>
                </template>
                <i class="text-teal-04 material-icons ml-2 cursor-pointer">info</i>
              </Tooltip> 
            </div>
          </template>
        </td>
      </tr>
    </template>
    <template #action="{ rowIndex, row }">
      <td>
        <template v-if="!disabled && !preview">
          <template v-if="reconcile">
            <Button @click="reconcileItem(row)" color="teal" dense>
              <p class="flex items-center gap-2 ml-3">
                Reconcile <i class="material-icons">chevron_right</i>
              </p>
            </Button>
          </template>
          <template v-else>
            <a
              href=""
              @click.prevent="deleteItem(rowIndex, row)"
              class="px-3 py-2 flex items-center justify-center"
            >
              <i class="material-icons">close</i>
            </a>
          </template>
        </template>
      </td>
    </template>
  </BTable>
</template>

<script>
import { computed, defineComponent, watch } from 'vue'

import BTable from 'components/BTable/BTable.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'

import Currency from 'components/Currency.vue'

import { money } from 'utils/number'

export default defineComponent({
  name: 'BulkQuoteItemTable',
  components: { BTable, Input, Button, Currency },
  props: {
    items: {
      type: Array
    },
    percentage: {
      type: Number,
      default: 100
    },
    preview: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: true
    },
    disabledStatus: {
      type: Boolean,
      default: true
    },
    reconcile: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:items', 'deleteItem', 'editItem', 'reconcileItem'],
  setup(props, { emit }) {
    const data = {
      items: []
    }
    const items = computed({
      get: () => {
        return (props.items && props.items) || data.items
      },
      set: (items = []) => {
        emit('update:items', items)
        data.items = items
      }
    })
    const itemConditions = computed(() => {
      const isEmptyItem = items.value.length < 1
      if (isEmptyItem) return []
      
      const uniqueConditions = new Map()
      
      items.value.forEach(item => {
        const { conditions = {} } = item
        const { data: conditionData = [] } = conditions
        
        conditionData.forEach(condition => {
          if (!uniqueConditions.has(condition.name)) {
            uniqueConditions.set(condition.name, {
              name: condition.name,
              rules: condition.rule
            })
          }
        })
      })

      return Array.from(uniqueConditions.values())
    })

    const tableHeaders = computed(() => {
      let headers = [
        {
          key: 'product_name',
          column_name: props.preview ? 'Device' : 'Item',
          meta: {}
        },
        {
          key: 'qty',
          column_name: 'Qty',
          meta: {}
        }
      ]
      itemConditions.value.forEach((condition) => {
        headers.push({
          key: transformConditionNameToKey(condition.name),
          column_name: condition.name,
          meta: {
            rules: transformConditionRules(condition.rules)
          }
        })
      })
      return headers
    })

    const tableData = computed(() => {
      return items.value.map((item) => {
        const { conditions = {} } = item
        const { data: conditionData = [] } = conditions
        
        const existingConditions = Object.fromEntries(
          conditionData.map((condition) => [
            transformConditionNameToKey(condition.name),
            {
              ...condition,
              price: condition.price || 0
            }
          ])
        )
        
        const normalizedConditions = {}
        itemConditions.value.forEach(condition => {
          const key = transformConditionNameToKey(condition.name)
          normalizedConditions[key] = existingConditions[key] || {
            name: condition.name,
            price: 0,
            is_manually_priced: false
          }
        })
        
        return {
          ...item,
          ...normalizedConditions
        }
      })
    })
    const calculateTotalQuantity = computed(() => {
      return items.value.reduce(
        (acc, item) => acc + (parseInt(item.qty) || 0),
        0
      )
    })
    const decimalPercentage = computed(
      () => (parseInt(`${props.percentage}`) || 0) / 100
    )
    const calculateConditionPrices = computed(() => {
      const conditionPrices = {}
      
      itemConditions.value.forEach(condition => {
        conditionPrices[condition.name] = 0
      })
      
      items.value.forEach(item => {
        const { conditions = {} } = item
        const { data: conditionData = [] } = conditions
        const quantity = parseFloat(item.qty) || 0
        
        conditionData.forEach((condition) => {
          const price = parseFloat(condition.price) || 0
          conditionPrices[condition.name] = (conditionPrices[condition.name] || 0) + price * quantity
        })
      })
      
      return Object.values(conditionPrices)
    })

    const calculateCommissionPrices = computed(() => {
      const commissionPrices = {}

      itemConditions.value.forEach(condition => {
        commissionPrices[condition.name] = 0
      })

      items.value.forEach(item => {
        const { conditions = {} } = item
        const { data: conditionData = [] } = conditions
        const quantity = parseFloat(item.qty) || 0

        conditionData.forEach(condition => {
          const price = parseFloat(condition.price) || 0
          const priceFromSource = parseFloat(condition.price_from_source) || 0
          const diff = priceFromSource - price

          commissionPrices[condition.name] = (commissionPrices[condition.name] || 0) + diff * quantity
        })
      })

      return Object.values(commissionPrices)
    })


    const tableFooter = computed(() => {
      const totalQuantity = calculateTotalQuantity.value
      const conditionPrices = calculateConditionPrices.value || []

      return ['Total', totalQuantity, ...conditionPrices, '']
    })

    const commissionFooter = computed(() => {
      const commissionPrices = calculateCommissionPrices.value || []
      const emptyColumns = Array(itemConditions.value.length - commissionPrices.length).fill(0)

      return ['Commission', '', ...commissionPrices, ...emptyColumns, '']
    })

    const emitEditItemEvent = (item) => {
      emit('editItem', item)
    }
    const transformConditionNameToKey = (conditionName) => {
      // replace all non-alphanumeric characters with underscores
      return `condition_${conditionName}`
    }
    const transformConditionRules = (rules) => {
      let newRules = rules ? JSON.parse(rules) : []
      if (
        Array.isArray(newRules) &&
        newRules.length === 1 &&
        newRules.includes(null)
      ) newRules = []

      return newRules || []
    }

    const getItemConditionByName = (items = [], conditionName) => {
      return items.findIndex((item) => item.name === conditionName)
    }
    const updateConditionPrice = (
      itemIndex,
      condition,
      newPrice,
      options = { isManuallyPriced: false }
    ) => {
      const currentItems = [...items.value]
      const hasNotMatchedItem = itemIndex < 0
      if (hasNotMatchedItem) return
      const conditionData = currentItems[itemIndex].conditions.data
      const matchedConditionIndex = getItemConditionByName(
        conditionData,
        condition.name
      )
      
      if (matchedConditionIndex === -1) {
        currentItems[itemIndex].conditions.data.push({
          name: condition.name,
          price: newPrice,
          is_manually_priced: options.isManuallyPriced || false,
          original_price: newPrice,
          rule: condition.rule || null
        })
      } else {
        currentItems[itemIndex].conditions.data[matchedConditionIndex].price =
          newPrice
        currentItems[itemIndex].conditions.data[
          matchedConditionIndex
        ].is_manually_priced = options.isManuallyPriced || false
      }
      
      items.value = currentItems
      if (options.isManuallyPriced) emitEditItemEvent(currentItems[itemIndex])
    }
    const updateQty = (itemIndex, quantity = 1) => {
      const currentItems = [...items.value]

      const hasNotMatchedItem = itemIndex < 0
      if (hasNotMatchedItem) return
      currentItems[itemIndex].qty = quantity
      items.value = currentItems
      emitEditItemEvent(currentItems[itemIndex])
    }
    const updateName = (itemIndex, name) => {
      const currentItems = [...items.value]
      const hasNotMatchedItem = itemIndex < 0
      if (hasNotMatchedItem) return
      currentItems[itemIndex].product_name = name
      items.value = currentItems
      emitEditItemEvent(currentItems[itemIndex])
    }
    const deleteItem = (itemIndex, item) => {
      const currentItems = [...items.value]
      const hasNotMatchedItem = itemIndex < 0
      if (hasNotMatchedItem) return
      currentItems.splice(itemIndex, 1)
      items.value = currentItems
      emit('deleteItem', item)
    }
    const reconcileItem = (item) => {
      emit('reconcileItem', item)
    }
    const updateAllConditionPricesByPercentage = () => {
      const currentItems = [...items.value]
      currentItems.forEach((item, itemIndex) => {
        const { conditions = {} } = item || {}
        const { data: conditionData = [] } = conditions
        conditionData.forEach((condition, index) => {
          const isManuallyPriced = condition.is_manually_priced
          if (isManuallyPriced) return
          const calculatedPrice =
            condition.original_price * decimalPercentage.value
          updateConditionPrice(itemIndex, condition, calculatedPrice)
        })
      })
    }
    watch(decimalPercentage, updateAllConditionPricesByPercentage)

    return {
      items,
      itemConditions,
      tableHeaders,
      tableData,
      tableFooter,
      commissionFooter,
      updateConditionPrice,
      updateQty,
      updateName,
      deleteItem,
      reconcileItem,
      money
    }
  }
})
</script>

<style scoped lang="scss">
.number-column {
  @apply text-right #{!important};
}

.b-table__footer {
  .b-table__row {
    @apply border-t border-teal-02;
  }
}

.table-preview {
  @apply border-0;
  :deep(.b-table__header) {
    background-color: rgba(255, 255, 255, 0);

    tr {
      @apply border-b-2 border-teal-04;
    }
  }

  :deep(.b-table__row) {
    &:hover {
      background-color: unset;
    }
  }

  :deep(.b-table__footer) {
    background-color: rgba(255, 255, 255, 0);

    tr {
      @apply border-0;
    }
  }

  .column-normal {
    @apply font-normal;
  }
}
</style>