<template>
  <ProductSearch
    v-model:query="querySearch"
    @search="handleSearchItems"
    @select="handleSelectItem"
    :label="null"
    :products="products"
    :categories="categories"
    :loading="loadingSearchItems"
    :show-add-custom-product="false"
    :active-product-unprice="isProductActiveButNoPrice"
    placeholder="Find or add an item"
  />
</template>

<script>
import { defineComponent } from 'vue'

import { useSearchItem } from 'composable/bulkquote'

import ProductSearch from 'components/Device/Form/DeviceSearch.vue'
import debounce from 'utils/debounce'

export default defineComponent({
  name: 'BulkQuoteItemSearch',
  components: { ProductSearch },
  props: {
    clearOnSelect: {
      type: Boolean,
      default: true
    }
  },
  emits: ['select'],
  setup(props, { emit }) {
    const {
      querySearch,
      products,
      categories,
      loading: loadingSearchItems,
      searchItems,
      isProductActiveButNoPrice
    } = useSearchItem()

    const handleSearchItems = debounce(() => searchItems(), 500)
    const handleSelectItem = (item) => {
      emit('select', item)
      if (props.clearOnSelect) {
        querySearch.value = ''
        return
      }
      querySearch.value = item.product_name
    }
    return {
      querySearch,
      products,
      categories,
      loadingSearchItems,
      isProductActiveButNoPrice,
      handleSearchItems,
      handleSelectItem
    }
  }
})
</script>

