<template>
  <div v-if="show">
    <label class="font-medium mb-2">Quoted Price</label>
    <div class="flex items-center gap-4">
      <span>0%</span>
      <BSlider
        class="flex-grow"
        v-model.number="percentage"
        :disabled="disabled"
        block
      />
      <span>100%</span>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, reactive } from 'vue'
import BSlider from 'components/BSlider/BSlider.vue'

export default defineComponent({
  name: 'BulkQuoteItemPercentage',
  components: { BSlider },
  props: {
    modelValue: {
      type: Number
    },
    disabled: {
      type: Boolean,
      default: false
    },
    create: {
      type: Boolean,
      default: false
    },
    order: {
      type: Object,
      default: () => ({
        id: ''
      })
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const data = reactive({
      percentage: 0
    })
    const show = computed(() => {
      if (!!props.create) return true

      if (!!props.order && !!props.order?.status_id && props.order?.status_id === 9000) return true

      return false
    })
    const percentage = computed({
      get: () => (props.modelValue && props.modelValue) || data.percentage,
      set: (newValue = 0) => {
        emit('update:modelValue', newValue)
        data.percentage = newValue
      }
    })
    return {
      show,
      percentage
    }
  }
})
</script>

<style scoped lang="scss"></style>
