<template>
  <div
    :class="[
      'b-slider',
      block && 'b-slider--block',
      disabled && 'b-slider--disabled'
    ]"
  >
    <input
      :disabled="disabled"
      ref="refSlider"
      class="b-slider__slider"
      v-model="value"
      type="range"
      :min="min"
      :max="max"
      :step="1"
    />
    <div ref="refSliderTooltip" class="b-slider__tooltip">
      <slot name="tooltip" v-bind="{ value }">
        <span>{{ sliderPercentage }}</span>
      </slot>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, reactive, ref, watch } from 'vue'

export default {
  name: 'BSlider',
  props: {
    modelValue: {
      type: Number,
      default: 0
    },
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    },
    step: {
      type: Number,
      default: 1
    },
    block: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const refSliderTooltip = ref(null)
    const refSlider = ref(null)
    const data = reactive({
      value: 0
    })
    const value = computed({
      get: () => (props.modelValue && props.modelValue) || data.value,
      set: (newValue = 0) => {
        emit('update:modelValue', newValue)
        data.value = newValue
      }
    })
    const sliderBasePercentage = computed(() =>
      Math.round((value.value / props.max) * 100)
    )
    const sliderPercentage = computed(() => {
      return `${sliderBasePercentage.value}%`
    })

    const calculateSliderTooltipPosition = () => {
      // calculate the position of the slider tooltip relative to the slider
      // const sliderRect = refSlider.value.getBoundingClientRect()
      const sliderTooltipRect = refSliderTooltip.value.getBoundingClientRect()

      const newVal = Number(
        ((value.value - props.min) * 100) / (props.max - props.min)
      )
      refSliderTooltip.value.style.left = `calc((${newVal}% - ${sliderTooltipRect.width / 2}px) + (${8 - newVal * 0.15}px))`
    }

    watch(sliderPercentage, calculateSliderTooltipPosition)

    onMounted(() => {
      calculateSliderTooltipPosition()
    })

    return {
      refSliderTooltip,
      refSlider,
      value,
      sliderPercentage
    }
  }
}
</script>

<style scoped lang="scss">
.b-slider {
  @apply inline-block relative;
  &--block {
    @apply block;
  }
  &--disabled{
    @apply opacity-50;
  }

  &__slider {
    @apply w-full;
    ::-webkit-slider-runnable-track {
      @apply w-full;
    }
  }

  &__tooltip {
    @apply bg-grey-04 text-white rounded-lg box-content py-2 px-4 absolute -top-12;
  }
}

input[type='range'] {
  cursor: pointer;
}

input[type='range'],
input[type='range']::-webkit-slider-runnable-track,
input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type='range']::-webkit-slider-runnable-track {
  height: 10px;
  background: linear-gradient(
      to right,
      theme('colors.teal.04'),
      theme('colors.teal.04')
    ),
    theme('colors.grey.01');
  background-size: v-bind(sliderPercentage) 100%;
  background-repeat: no-repeat;
  border-radius: 5px;
}

input[type='range']::-moz-range-track {
  height: 10px;
  background: linear-gradient(
      to right,
      theme('colors.teal.04'),
      theme('colors.teal.04')
    ),
    theme('colors.grey.01');
  background-size: v-bind(sliderPercentage) 100%;
  background-repeat: no-repeat;
  border-radius: 5px;
}

input[type='range']::-webkit-slider-thumb {
  @apply bg-white h-6 w-6 -mt-1.5 shadow;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
</style>